body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home{height:90vh}.shop{display:flex;justify-content:space-evenly}.navbar{display:flex;justify-content:space-between;height:12vh}.navbar .logo{width:40%;display:flex;justify-content:center;font-family:"Kalam";font-size:0.7em}.navbar .navbar-items{width:60%;display:flex;justify-content:space-evenly}@media only screen and (min-width: 600px){.navbar{width:100%}.navbar .logo{width:10%;font-size:1em}.navbar .navbar-items{width:40%}}@font-face{font-family:"Kalam";font-style:normal;font-weight:300;font-display:swap;src:local("Kalam Light"),local("Kalam-Light"),url(https://fonts.gstatic.com/s/kalam/v10/YA9Qr0Wd4kDdMtD6GjLMkiQ.woff2) format("woff2");unicode-range:U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD}.jumbotron{display:flex;justify-content:center;align-items:center;height:50vh;width:100%;background:black;color:white}.jumbotron h1{font-family:"Kalam"}.item{display:flex;flex-direction:column;width:300px;margin:10px;padding:40px;box-shadow:5px 3px 10px gray}.item .title{display:flex;justify-content:center;text-align:center}.item .title h1{margin-top:20px;border-radius:5px;font-size:1.2em;font-family:"Kalam";width:100%}.item .picture{display:flex;justify-content:center;align-items:center}.item .picture div{width:250px;height:300px;padding:50px}.item .price{display:flex;flex-direction:column;justify-content:center;align-items:center}.item .price p{font-size:1em;font-weight:bold;font-family:"Kalam"}.gallery{width:650px;max-height:600px;padding:0px 50px;z-index:1}.gallery .picture{height:600px}.specification{height:100vh;display:flex;flex-direction:column}.specification .title-spec{display:flex;justify-content:center;align-items:center}.specification .title-spec h1{font-family:"Kalam"}.specification .product-spec{display:flex}.specification .product-spec .detail-spec{display:flex;flex-direction:column;justify-content:center;align-items:center;width:50%}.specification .product-spec .detail-spec a{background:rgba(2,0,251,0.7);text-decoration:none;color:white;padding:10px;margin:5px;border-radius:5px}.banner{position:absolute;top:50%}.banner h2{width:100%;padding:10px;z-index:1;background:red;color:white}

